import React from 'react'
import home from '../../../assets/home.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook, faTwitter, faYoutube, faPinterest, faDribbble, faInstagram } from '@fortawesome/free-brands-svg-icons'
const Home = () => {
  return (
    <>
      <section className="home">
        <div className="container flex">
          <div className="left">
            <div className="img">
              <img src={home} alt=""/>
            </div>
          </div>

          <div className="right topMargin">
            <h1>
              I AM A <br />
              Web Designer
            </h1>
            <div className="scoialIcon">
            <i className='facebook'><FontAwesomeIcon icon={faFacebook} /></i>
            <i className='twitter'><FontAwesomeIcon icon={faTwitter} /></i>
            <i className='youtube'><FontAwesomeIcon icon={faYoutube} /></i>
            <i className='instagram'><FontAwesomeIcon icon={faInstagram} /></i>
            <i className='pinterest'><FontAwesomeIcon icon={faPinterest} /></i>
            <i className='dribble'><FontAwesomeIcon icon={faDribbble} /> </i> 
            </div>
           <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p> 
          <button className="primary-btn">
            Contact Us
          </button>
          </div>
        </div>
      </section>
    </>
  )
}

export default Home
